import React, { FC, useState, useRef, useEffect } from 'react';
import { Email, CharacterLock } from '@rsuite/icons';
import classes from './Registration.module.css';
import FormInputList from '../../components/FormInputList/FormInputList';
import { Button, FormInstance } from 'rsuite';
import { Link } from 'react-router-dom';
import { registrationModel } from '../../models';
import { useShowMessage } from '../../hooks/useShowMessage';
import Text from '../../components/UI/Text/Text';
import { useTypedSelector } from '../../hooks/useTypedSelector';
import { useDispatch } from 'react-redux';
import { userRegistration } from '../../store/action-creators/user';


const dataInputs = {
    email: '',
    password: '',
    verify: '',
}

const formInputs = [
    {
        name: 'email',
        label: <><Email />  </>,
        rest: {
            type: "email",
        },
    },
    {
        name: 'password',
        label: <><CharacterLock /> </>,
        rest: {
            type: "password",
            autoComplete: "off",
        },
    },
    {
        name: 'verify',
        label: <><CharacterLock />  </>,
        rest: {
            type: "password",
            autoComplete: "off",
        },
    },
]


const Registration: FC = () => {
    const { isAuth, data, loading, error } = useTypedSelector(state => state.user);
    const dispatch: any = useDispatch();

    const [formValue, setFormValue] = useState(dataInputs);
    const formRef = useRef<FormInstance<Record<string, any>>>(null);

    const showMessage = useShowMessage();

    const handleSubmit = () => {
        if (!formRef.current?.check()) {
            showMessage(' !', 'error');
            return;
        }

        dispatch(userRegistration(formValue.email, formValue.email, formValue.password));
    }

    const formButtons = [
        <Button appearance='primary' key='0' onClick={handleSubmit}></Button>,
        <div className={classes.center} key='1'>
              ?
            <Link to='/login'>
                <Button appearance='link'></Button>
            </Link>
        </div>,
    ]

    useEffect(() => {
        if (error === 'error on registration') {
            showMessage('    !', 'error');
        }
    }, [error])


    return (
        <div className={classes.registration}>
            <div className={classes.title}>
                <Text size='df'>  </Text>
            </div>
            <FormInputList
                formValue={formValue}
                setFormValue={setFormValue}
                formRef={formRef}
                formInputs={formInputs}
                formButtons={formButtons}
                model={registrationModel}
            />
        </div>
    );
}


export default Registration;